import { useParams } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { apartmentsApi } from '../api/client';
import { Apartment } from '../api/types';

export default function ApartmentDetailsPage() {
  const { id } = useParams<{ id: string }>();

  const { data: response, isLoading } = useQuery({
    queryKey: ['apartment', id],
    queryFn: () => apartmentsApi.getById(Number(id)),
  });

  const apartment = response?.data?.data;

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (!apartment) {
    return (
      <div className="text-center py-12">
        <svg
          className="mx-auto h-12 w-12 text-gray-400"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
          />
        </svg>
        <h3 className="mt-2 text-sm font-medium text-gray-900">
          Объявление не найдено
        </h3>
        <p className="mt-1 text-sm text-gray-500">
          Возможно, оно было удалено или перемещено
        </p>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="lg:grid lg:grid-cols-2 lg:gap-x-8 lg:items-start">
        {/* Image gallery */}
        <div className="flex flex-col-reverse">
          <div className="hidden mt-6 w-full max-w-2xl mx-auto sm:block lg:max-w-none">
            <div className="grid grid-cols-4 gap-4">
              {apartment.images.map((image: string, index: number) => (
                <div
                  key={index}
                  className="relative rounded-lg overflow-hidden group"
                >
                  <img
                    src={image}
                    alt={`${apartment.title} - фото ${index + 1}`}
                    className="w-full h-full object-center object-cover"
                  />
                </div>
              ))}
            </div>
          </div>

          <div className="w-full aspect-w-1 aspect-h-1 rounded-lg overflow-hidden">
            <img
              src={apartment.images[0]}
              alt={apartment.title}
              className="w-full h-full object-center object-cover"
            />
          </div>
        </div>

        {/* Apartment info */}
        <div className="mt-10 px-4 sm:px-0 sm:mt-16 lg:mt-0">
          <h1 className="text-3xl font-extrabold tracking-tight text-gray-900">
            {apartment.title}
          </h1>

          <div className="mt-3">
            <h2 className="sr-only">Информация о квартире</h2>
            <p className="text-3xl text-gray-900">{apartment.price} ₽/мес</p>
          </div>

          <div className="mt-6">
            <h3 className="sr-only">Описание</h3>
            <div className="text-base text-gray-700 space-y-6">
              <p>{apartment.description}</p>
            </div>
          </div>

          <div className="mt-6">
            <div className="flex items-center">
              <div className="flex items-center">
                {[0, 1, 2, 3, 4].map((index) => (
                  <svg
                    key={index}
                    className={`h-5 w-5 flex-shrink-0 ${
                      index < (apartment.rating || 0)
                        ? 'text-yellow-400'
                        : 'text-gray-300'
                    }`}
                    fill="currentColor"
                    viewBox="0 0 20 20"
                  >
                    <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                  </svg>
                ))}
              </div>
              <p className="ml-3 text-sm text-gray-700">
                {apartment.rating || 0} отзывов
              </p>
            </div>
          </div>

          <div className="mt-8 border-t border-gray-200 pt-8">
            <h2 className="text-sm font-medium text-gray-900">Характеристики</h2>

            <div className="mt-4">
              <ul role="list" className="text-sm text-gray-700 space-y-4">
                <li className="flex justify-between">
                  <span>Город</span>
                  <span>{apartment.city}</span>
                </li>
                <li className="flex justify-between">
                  <span>Количество комнат</span>
                  <span>{apartment.rooms}</span>
                </li>
                <li className="flex justify-between">
                  <span>Площадь</span>
                  <span>{apartment.area} м²</span>
                </li>
                <li className="flex justify-between">
                  <span>Этаж</span>
                  <span>{apartment.floor}</span>
                </li>
                <li className="flex justify-between">
                  <span>Статус</span>
                  <span
                    className={`${
                      apartment.is_active
                        ? 'text-green-600'
                        : 'text-red-600'
                    }`}
                  >
                    {apartment.is_active ? 'Активно' : 'Неактивно'}
                  </span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 