import { useState } from 'react';
import { Link } from 'react-router-dom';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { apartmentsApi } from '../api/client';
import { Apartment } from '../api/types';

export default function FavoritesPage() {
  const queryClient = useQueryClient();
  const [sortBy, setSortBy] = useState<'price-asc' | 'price-desc' | 'date-desc'>(
    'date-desc'
  );

  const { data: response, isLoading } = useQuery({
    queryKey: ['favorites'],
    queryFn: () => apartmentsApi.getFavorites(),
  });

  const removeFromFavoritesMutation = useMutation({
    mutationFn: (id: number) => apartmentsApi.removeFromFavorites(id),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['favorites'] });
    },
  });

  const apartments = response?.data?.data || [];

  const sortedApartments = [...apartments].sort((a: Apartment, b: Apartment) => {
    if (sortBy === 'price-asc') return a.price - b.price;
    if (sortBy === 'price-desc') return b.price - a.price;
    return new Date(b.created_at).getTime() - new Date(a.created_at).getTime();
  });

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="sm:flex sm:items-center">
        <div className="sm:flex-auto">
          <h1 className="text-2xl font-semibold text-gray-900">
            Избранные объявления
          </h1>
          <p className="mt-2 text-sm text-gray-700">
            Список объявлений, которые вы добавили в избранное.
          </p>
        </div>
        <div className="mt-4 sm:mt-0 sm:ml-16 sm:flex-none">
          <select
            value={sortBy}
            onChange={(e) =>
              setSortBy(e.target.value as typeof sortBy)
            }
            className="block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
          >
            <option value="date-desc">Сначала новые</option>
            <option value="price-asc">По возрастанию цены</option>
            <option value="price-desc">По убыванию цены</option>
          </select>
        </div>
      </div>

      <div className="mt-8">
        <div className="flex justify-between items-center mb-4">
          <div className="text-sm text-gray-500">
            Всего объявлений: {apartments.length}
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {sortedApartments.map((apartment: Apartment) => (
            <Link
              key={apartment.id}
              to={`/apartments/${apartment.id}`}
              className="bg-white rounded-lg shadow overflow-hidden hover:shadow-lg transition-shadow duration-300"
            >
              <div className="aspect-w-16 aspect-h-9">
                <img
                  src={apartment.images[0] || '/placeholder.jpg'}
                  alt={apartment.title}
                  className="object-cover w-full h-48"
                />
              </div>
              <div className="p-4">
                <h3 className="text-lg font-semibold text-gray-900 mb-2">
                  {apartment.title}
                </h3>
                <p className="text-gray-600 mb-2">{apartment.city}</p>
                <div className="flex justify-between items-center">
                  <span className="text-xl font-bold text-blue-600">
                    {apartment.price.toLocaleString()} ₽/мес
                  </span>
                  <span className="text-sm text-gray-500">
                    {apartment.rooms} {apartment.rooms === 1 ? 'комната' : 'комнаты'}
                  </span>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>

      {apartments.length === 0 && (
        <div className="mt-8 text-center">
          <svg
            className="mx-auto h-12 w-12 text-gray-400"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
            />
          </svg>
          <h3 className="mt-2 text-sm font-medium text-gray-900">
            Нет избранных объявлений
          </h3>
          <p className="mt-1 text-sm text-gray-500">
            Добавьте объявления в избранное, чтобы они отображались здесь.
          </p>
          <div className="mt-6">
            <Link
              to="/"
              className="inline-flex items-center px-4 py-2 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
            >
              Перейти к поиску
            </Link>
          </div>
        </div>
      )}
    </div>
  );
} 