import axios from 'axios';
import { ApiResponse, Apartment, User } from './types';

const api = axios.create({
  baseURL: 'http://localhost:8000/api',
  headers: {
    'Content-Type': 'application/json',
  },
});

// Добавляем интерсептор для авторизации
api.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

export const apartmentsApi = {
  getAll: (filters?: any) => api.get<ApiResponse<Apartment[]>>('/apartments', { params: filters }),
  getById: (id: number) => api.get<ApiResponse<Apartment>>(`/apartments/${id}`),
  create: (apartment: Omit<Apartment, 'id' | 'created_at' | 'updated_at'>) =>
    api.post<ApiResponse<Apartment>>('/apartments', apartment),
  update: (id: number, apartment: Partial<Apartment>) =>
    api.put<ApiResponse<Apartment>>(`/apartments/${id}`, apartment),
  delete: (id: number) => api.delete<ApiResponse<void>>(`/apartments/${id}`),
  getFavorites: () => api.get<ApiResponse<Apartment[]>>('/apartments/favorites'),
  addToFavorites: (id: number) => api.post<ApiResponse<void>>(`/apartments/${id}/favorite`),
  removeFromFavorites: (id: number) => api.delete<ApiResponse<void>>(`/apartments/${id}/favorite`),
  getMyApartments: () => api.get<ApiResponse<Apartment[]>>('/apartments/my'),
};

export const usersApi = {
  getCurrentUser: () => api.get<ApiResponse<User>>('/users/me'),
  updateUser: (user: Partial<User>) => api.put<ApiResponse<User>>('/users/me', user),
  getActivityHistory: (params?: { type?: string; timeRange?: string }) =>
    api.get<ApiResponse<any[]>>('/users/activity', { params }),
  getNotificationSettings: () => api.get<ApiResponse<any>>('/users/notifications/settings'),
  updateNotificationSettings: (settings: any) =>
    api.put<ApiResponse<any>>('/users/notifications/settings', settings),
};

export const authApi = {
  login: (email: string, password: string) =>
    api.post<ApiResponse<{ token: string; user: User }>>('/auth/login', { email, password }),
  register: (data: { name: string; email: string; password: string }) =>
    api.post<ApiResponse<{ token: string; user: User }>>('/auth/register', data),
  forgotPassword: (email: string) =>
    api.post<ApiResponse<void>>('/auth/forgot-password', { email }),
  resetPassword: (token: string, password: string) =>
    api.post<ApiResponse<void>>('/auth/reset-password', { token, password }),
}; 