import { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { usersApi } from '../api/client';

interface NotificationSettings {
  email: {
    newMessages: boolean;
    newReviews: boolean;
    priceChanges: boolean;
    similarApartments: boolean;
  };
  push: {
    newMessages: boolean;
    newReviews: boolean;
    priceChanges: boolean;
    similarApartments: boolean;
  };
  telegram: {
    newMessages: boolean;
    newReviews: boolean;
    priceChanges: boolean;
    similarApartments: boolean;
  };
}

export default function NotificationSettingsPage() {
  const queryClient = useQueryClient();
  const [settings, setSettings] = useState<NotificationSettings>({
    email: {
      newMessages: true,
      newReviews: true,
      priceChanges: true,
      similarApartments: true,
    },
    push: {
      newMessages: true,
      newReviews: true,
      priceChanges: true,
      similarApartments: true,
    },
    telegram: {
      newMessages: true,
      newReviews: true,
      priceChanges: true,
      similarApartments: true,
    },
  });

  const { data: response, isLoading } = useQuery({
    queryKey: ['notification-settings'],
    queryFn: () => usersApi.getNotificationSettings(),
  });

  const updateSettingsMutation = useMutation({
    mutationFn: (data: NotificationSettings) =>
      usersApi.updateNotificationSettings(data),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['notification-settings'] });
    },
  });

  const handleToggle = (
    channel: keyof NotificationSettings,
    setting: keyof NotificationSettings['email']
  ) => {
    setSettings((prev) => ({
      ...prev,
      [channel]: {
        ...prev[channel],
        [setting]: !prev[channel][setting],
      },
    }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    updateSettingsMutation.mutate(settings);
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <div className="md:grid md:grid-cols-3 md:gap-6">
        <div className="md:col-span-1">
          <div className="px-4 sm:px-0">
            <h3 className="text-lg font-medium leading-6 text-gray-900">
              Настройки уведомлений
            </h3>
            <p className="mt-1 text-sm text-gray-600">
              Управление предпочтениями по уведомлениям.
            </p>
          </div>
        </div>

        <div className="mt-5 md:mt-0 md:col-span-2">
          <form onSubmit={handleSubmit}>
            <div className="shadow sm:rounded-md sm:overflow-hidden">
              <div className="px-4 py-5 bg-white space-y-6 sm:p-6">
                <div className="space-y-6">
                  <div>
                    <h4 className="text-lg font-medium text-gray-900">
                      Email уведомления
                    </h4>
                    <div className="mt-4 space-y-4">
                      {Object.entries(settings.email).map(([key, value]) => (
                        <div key={key} className="flex items-center">
                          <input
                            type="checkbox"
                            id={`email-${key}`}
                            checked={value}
                            onChange={() =>
                              handleToggle('email', key as keyof NotificationSettings['email'])
                            }
                            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                          />
                          <label
                            htmlFor={`email-${key}`}
                            className="ml-3 block text-sm font-medium text-gray-700"
                          >
                            {key === 'newMessages' && 'Новые сообщения'}
                            {key === 'newReviews' && 'Новые отзывы'}
                            {key === 'priceChanges' && 'Изменения цен'}
                            {key === 'similarApartments' && 'Похожие объявления'}
                          </label>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="border-t border-gray-200 pt-6">
                    <h4 className="text-lg font-medium text-gray-900">
                      Push уведомления
                    </h4>
                    <div className="mt-4 space-y-4">
                      {Object.entries(settings.push).map(([key, value]) => (
                        <div key={key} className="flex items-center">
                          <input
                            type="checkbox"
                            id={`push-${key}`}
                            checked={value}
                            onChange={() =>
                              handleToggle('push', key as keyof NotificationSettings['push'])
                            }
                            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                          />
                          <label
                            htmlFor={`push-${key}`}
                            className="ml-3 block text-sm font-medium text-gray-700"
                          >
                            {key === 'newMessages' && 'Новые сообщения'}
                            {key === 'newReviews' && 'Новые отзывы'}
                            {key === 'priceChanges' && 'Изменения цен'}
                            {key === 'similarApartments' && 'Похожие объявления'}
                          </label>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="border-t border-gray-200 pt-6">
                    <h4 className="text-lg font-medium text-gray-900">
                      Telegram уведомления
                    </h4>
                    <div className="mt-4 space-y-4">
                      {Object.entries(settings.telegram).map(([key, value]) => (
                        <div key={key} className="flex items-center">
                          <input
                            type="checkbox"
                            id={`telegram-${key}`}
                            checked={value}
                            onChange={() =>
                              handleToggle(
                                'telegram',
                                key as keyof NotificationSettings['telegram']
                              )
                            }
                            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                          />
                          <label
                            htmlFor={`telegram-${key}`}
                            className="ml-3 block text-sm font-medium text-gray-700"
                          >
                            {key === 'newMessages' && 'Новые сообщения'}
                            {key === 'newReviews' && 'Новые отзывы'}
                            {key === 'priceChanges' && 'Изменения цен'}
                            {key === 'similarApartments' && 'Похожие объявления'}
                          </label>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </div>

              <div className="px-4 py-3 bg-gray-50 text-right sm:px-6">
                <button
                  type="submit"
                  className="inline-flex justify-center py-2 px-4 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                >
                  Сохранить настройки
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
} 