import { useState } from 'react';
import { useQuery } from '@tanstack/react-query';
import { Link } from 'react-router-dom';
import { apartmentsApi } from '../api/client';
import { Apartment } from '../api/types';

interface SearchFilters {
  city?: string;
  minPrice?: number;
  maxPrice?: number;
  rooms?: number;
  sortBy?: 'price_asc' | 'price_desc' | 'date_desc';
}

export default function HomePage() {
  const [filters, setFilters] = useState<SearchFilters>({
    sortBy: 'date_desc',
  });

  const { data: response, isLoading, error } = useQuery({
    queryKey: ['apartments', filters],
    queryFn: () => apartmentsApi.getAll(filters),
    retry: 1,
  });

  const apartments = response?.data?.data || [];

  const handleFilterChange = (key: keyof SearchFilters, value: string | number | undefined) => {
    setFilters((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center py-12">
        <svg
          className="mx-auto h-12 w-12 text-red-500"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
          />
        </svg>
        <h3 className="mt-2 text-sm font-medium text-gray-900">
          Произошла ошибка при загрузке данных
        </h3>
        <p className="mt-1 text-sm text-gray-500">
          Пожалуйста, проверьте подключение к серверу и попробуйте снова
        </p>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Hero Section */}
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">
          Найдите идеальное жилье для аренды
        </h1>
        <p className="text-xl text-gray-600">
          Большой выбор квартир и домов в аренду
        </p>
      </div>

      {/* Search Filters */}
      <div className="bg-white rounded-lg shadow p-6 mb-8">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <div>
            <label htmlFor="city" className="block text-sm font-medium text-gray-700">
              Город
            </label>
            <input
              type="text"
              id="city"
              value={filters.city || ''}
              onChange={(e) => handleFilterChange('city', e.target.value || undefined)}
              className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
              placeholder="Введите город"
            />
          </div>

          <div>
            <label htmlFor="rooms" className="block text-sm font-medium text-gray-700">
              Количество комнат
            </label>
            <select
              id="rooms"
              value={filters.rooms || ''}
              onChange={(e) => handleFilterChange('rooms', e.target.value ? Number(e.target.value) : undefined)}
              className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
            >
              <option value="">Любое</option>
              <option value="1">1 комната</option>
              <option value="2">2 комнаты</option>
              <option value="3">3 комнаты</option>
              <option value="4">4+ комнаты</option>
            </select>
          </div>

          <div>
            <label htmlFor="price" className="block text-sm font-medium text-gray-700">
              Цена
            </label>
            <div className="mt-1 flex space-x-2">
              <input
                type="number"
                id="minPrice"
                value={filters.minPrice || ''}
                onChange={(e) => handleFilterChange('minPrice', e.target.value ? Number(e.target.value) : undefined)}
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                placeholder="От"
              />
              <input
                type="number"
                id="maxPrice"
                value={filters.maxPrice || ''}
                onChange={(e) => handleFilterChange('maxPrice', e.target.value ? Number(e.target.value) : undefined)}
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                placeholder="До"
              />
            </div>
          </div>

          <div>
            <label htmlFor="sort" className="block text-sm font-medium text-gray-700">
              Сортировка
            </label>
            <select
              id="sort"
              value={filters.sortBy}
              onChange={(e) => handleFilterChange('sortBy', e.target.value as SearchFilters['sortBy'])}
              className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
            >
              <option value="date_desc">Сначала новые</option>
              <option value="price_asc">По возрастанию цены</option>
              <option value="price_desc">По убыванию цены</option>
            </select>
          </div>
        </div>
      </div>

      {/* Apartments Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {apartments.map((apartment: Apartment) => (
          <Link
            key={apartment.id}
            to={`/apartments/${apartment.id}`}
            className="bg-white rounded-lg shadow overflow-hidden hover:shadow-lg transition-shadow duration-300"
          >
            <div className="aspect-w-16 aspect-h-9">
              <img
                src={apartment.images[0] || '/placeholder.jpg'}
                alt={apartment.title}
                className="object-cover w-full h-48"
              />
            </div>
            <div className="p-4">
              <h3 className="text-lg font-semibold text-gray-900 mb-2">
                {apartment.title}
              </h3>
              <p className="text-gray-600 mb-2">{apartment.city}</p>
              <div className="flex justify-between items-center">
                <span className="text-xl font-bold text-blue-600">
                  {apartment.price.toLocaleString()} ₽/мес
                </span>
                <span className="text-sm text-gray-500">
                  {apartment.rooms} {apartment.rooms === 1 ? 'комната' : 'комнаты'}
                </span>
              </div>
            </div>
          </Link>
        ))}
      </div>

      {apartments.length === 0 && (
        <div className="text-center py-12">
          <svg
            className="mx-auto h-12 w-12 text-gray-400"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4"
            />
          </svg>
          <h3 className="mt-2 text-sm font-medium text-gray-900">
            Объявления не найдены
          </h3>
          <p className="mt-1 text-sm text-gray-500">
            Попробуйте изменить параметры поиска
          </p>
        </div>
      )}
    </div>
  );
} 